import logo from './logo.svg';
import './App.css';
import { useState } from 'react';

function App() {
  const [listContainer, setListContainer] = useState([]);
  const [listEntry, setListEntry] = useState("");

  return (
    <div className="App">
      <div className="app-container" style={{ display: "flex", flexDirection: "column", justifyContent: "center"}}>
        <header className="App-header">
          <h2>Decidr</h2>
        </header>
        <div className="list-display-container" >
          {
            listContainer.map(entry => <p>{entry}</p>)
          }
        </div>
        {/* <div className="list-display-selection" style={{ fontSize: 36 }}>
          {selectedEntry && <p>{selectedEntry}</p>}
        </div> */}
        <div className="list-form-container" style={{ display: "flex", justifyContent: "center", margin: "0.5em" }}>
          <input type="text" placeholder="Type here!" name="listEntry" onChange={event => setListEntry(event.target.value)}/>
          <button type="submit" onClick={(event) => {
            setListContainer([ ...listContainer, listEntry ]);
            setListEntry("");
          }}>Add an Item</button>
        </div>
        <button style={{ alignSelf: "center" }}>Select an Item</button>
      </div>
    </div>
  );
}

export default App;
